<template>
  <div :class="'home_box' + (mobile ? '_APP' : '_PC')">
    <!-- 标题 -->
    <el-carousel :height="mobile ? '240px' : '900px'" arrow="never" indicator-position v-if="!isArticle" :autoplay="false">
      <el-carousel-item>
        <el-image class="carousel_picture" :src="require('../assets/resource/home/banner1.png')" fit="cover"></el-image>
      </el-carousel-item>
      <el-carousel-item>
        <el-image class="carousel_picture" :src="require('../assets/resource/home/banner2.png')" fit="cover"></el-image>
      </el-carousel-item>
      <el-carousel-item>
        <el-image class="carousel_picture" :src="require('../assets/resource/home/banner3.png')" fit="cover"></el-image>
      </el-carousel-item>
    </el-carousel>

    <!-- 服务内容 -->
    <businessCom v-if="!isArticle"></businessCom>

    <!-- 服务设备 -->
    <journalismCom v-if="!isArticle"> </journalismCom>

    <!-- 服务软件 -->
    <cooperationCom v-if="!isArticle"></cooperationCom>

    <Article v-if="isArticle" @articleBackFunc="articleBackFunc" :articleData="articleData"
      :articleImgList="articleImgList" goBackPage="首页"></Article>
  </div>
</template>
<script>
import { mapActions } from "vuex";
import businessCom from "@/views/HomeCom/businessCom";
import cooperationCom from "@/views/HomeCom/cooperationCom";
import journalismCom from "@/views/HomeCom/journalismCom";
import '../style/PC/Home.scss'
import '../style/APP/Home.scss'
export default {
  components: {
    businessCom,
    cooperationCom,
    journalismCom,
  },
  data() {
    return {
      isArticle: false,
      articleData: {},
      articleImgList: [],
    };
  },
  created() {
    this.getTokenByForm();
  },
  computed: {},
  mounted() { },
  methods: {
    ...mapActions({
      setfirstOpen: "user/setfirstOpen",
    }),
    // 登录接口
    getTokenByForm() {
      let val = {
        adminToken: "0f3a75da2580415eeff23f1913ddd035",
      };
      this.setfirstOpen(val);
    },
    articleBackFunc() {
      this.isArticle = false;
      window.scrollTo(0, 0);
    },
  },
};
</script>