<template>
  <!-- 业务 -->
  <div  :class="'device_box' + (mobile ? '_APP' : '_PC')">
    <HomeTitle title="服・务・设・备" subheading="SERVICE DEVICE"></HomeTitle>
    <div class="device_container_box ">
      <div class="device_container_list">
        <div
          :class="[
            'device_container_item',
            'animate__animated animate__fadeIn',
          ]"
          v-for="(item, index) in deviceList"
          @mouseenter="handleMouseEnter(index)"
          @mouseleave="handleMouseLeave"
          :key="index"
          :style="{
            backgroundImage:
              'url(' +
              require(`../../assets/resource/home/${item.img + '.png'}`) +
              ')',
          }"
        >
          <div
            class="device_container_name animate__animated animate__fadeIn"
            v-if="expandedIndex !== index"
          >
            {{ item.name }}
          </div>
          <div
            class="device_pitch animate__animated animate__fadeInUp"
            v-if="expandedIndex == index"
          >
            <div class="device_pitch_name">
              {{ item.name }}
            </div>
            <div class="device_pitch_content">
              {{ item.content }}
            </div>
            <div class="device_pitch_more" @click="pitchMore">
              了解更多>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="view_more_box">
      <div class="view_more_btn">
        查看更多>
      </div>
    </div>
    <NoData v-if="deviceList.length == 0"></NoData>
  </div>
</template>

